.modal-enter .modalOverlay {
  opacity: 0;
}

.modal-enter.modal-enter-active .modalOverlay {
  opacity: 1;
  -webkit-transition: opacity 0.15s ease-in-out;
  -o-transition: opacity 0.15s ease-in-out;
  transition: opacity 0.15s ease-in-out;
}

.modal-enter .modalContent{
  opacity: 0;
  -webkit-transform: translate3d(0, -30%, 0);
          transform: translate3d(0, -30%, 0);
}

.modal-enter.modal-enter-active .modalContent {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  -webkit-transition: opacity 0.2s ease-in-out 0.2s, -webkit-transform 0.2s ease-in-out 0.2s;
  transition: opacity 0.2s ease-in-out 0.2s, -webkit-transform 0.2s ease-in-out 0.2s;
  -o-transition: opacity 0.2s ease-in-out 0.2s, transform 0.2s ease-in-out 0.2s;
  transition: opacity 0.2s ease-in-out 0.2s, transform 0.2s ease-in-out 0.2s;
  transition: opacity 0.2s ease-in-out 0.2s, transform 0.2s ease-in-out 0.2s, -webkit-transform 0.2s ease-in-out 0.2s;
}

.modal-exit {
  opacity: 1;
}

.modal-exit.modal-exit-active {
  opacity: 0;
  -webkit-transition: opacity 0.15s ease-in-out;
  -o-transition: opacity 0.15s ease-in-out;
  transition: opacity 0.15s ease-in-out;
}



